import { Container } from "@mui/material";
import MyPhoto from "../assets/Profile_soloNB.png"
import CV from "../Pages/CV";
import PDF_URL from "../assets/CV Vincent Lveill.pdf"


export default function MediaTexte() {


    const myText = <div>
        <h3>Rinventer le Web, Ligne par Ligne</h3>
        <div>
            Dveloppeur Web Front-End passionn, avide de rsoudre des nigmes
            numriques et de repousser les limites de la crativit.
            Je m'immerge dans les dfis du dveloppement pour mieux les dompter,
            et chaque obstacle est une opportunit d'apprentissage. Car pour moi,
            le dveloppement web est bien plus qu'une simple comptence technique :
            c'est une passion qui alimente ma soif de connaissance et d'innovation.
            Prt  explorer de nouveaux horizons et  transformer des ides en
            ralit digitale, je suis ici pour faonner l'avenir du web, une ligne
            de code  la fois.
        </div>
    </div>

    return (
        <>
            <Container sx={{
                display: "flex",
                justifyContent: "center",
                marginTop: 5,
                border: 2,
                backgroundColor: "#EFE1DD"
            }}
            >
                <h1>Dveloppeur Front-End</h1>
            </Container>
            <Container sx={{
                display: "flex",
                flexDirection: "column-reverse",
                justifyContent: "center",
                alignContent: "center",
                marginTop: 5,
                backgroundColor: "#EFE1DD",
                '@media (min-width: 900px)': {
                    flexDirection: 'row',
                },
            }}>
                <Container
                    sx={{
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                        alignItems: "center",
                        paddingBottom: 5
                    }}
                >
                    {myText}
                    <CV pdfUrl={PDF_URL} />
                </Container>

                <Container
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        padding: 2,
                        '@media (max-width: 900px)': {
                            width: "75%",
                        },
                    }}>
                    <img
                        src={MyPhoto}
                        style={{
                            width: "75%",
                            height: "auto",
                            borderRadius: "25%",
                        }} />
                </Container>
            </Container>
        </>
    )
}